*,
*::before,
*::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.wrapper{
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(#4f8bfa, #0231b5);
}
.list{
    width: 400px;
    min-height: 400px;
    max-width: 100%;
    padding: 30px;
    background-color: #f1f1f1;
    border-radius: 10px;
}
.input{
    margin-bottom: 20px;
    padding: 10px;
    width: 100%;
    border: none;
    outline: none;
    border-radius: 10px;
}
.header{
    margin-bottom: 50px;
    padding: 0 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.select{
    background-color: transparent;
    border: 2px solid #000;
    border-radius: 15%;
    outline: none;
}