.item{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    list-style-type: none;
}
.btn{
    background-color: transparent;
    border: none;
    opacity: 0.5;
    transform: scale(0.8);
    cursor: pointer;
    font-size: 20px;
    transition: 0.5s;
    &:hover{
    transform: scale(1);
    opacity: 1;
    }
}
.checkbox{
    cursor: pointer;
}
.text{
    display: block;
    text-align: center;
    width: 88%;
    min-height: 20px;
    overflow: auto;
}
